div.containerCartWidget{
  cursor: pointer ;
}


p.cartCounter {
  color: white;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 600;
  font-size: 16px;
  
}

.css-bb29op-MuiSvgIcon-root { /* LOGO DE CARRITO */
  color: white;
}

.cartWidgetContainer .MuiPaper-root.MuiPaper-elevation p {
  padding-left: 5px;

}

.cartWidgetContainer .MuiMenu-list{
  width: 350px;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 500px;
  

}

.cartWidgetContainer .MuiPaper-root.MuiPaper-elevation p {
  color: black;
}

div.cartWidget{
  display: flex;
}

div.cartWidgetImg {
  img {
  overflow: hidden;
  width: 85px;
  height: auto;
  }
}

div.CartWidgetInfo{
  a.productNameWidget{
    text-decoration: none;
    :visited{
      text-decoration: none;
      color: black;
    }
    
  }
  width: 60%;
  padding-left: 10px;
  padding-right: 10px;
  p{
    padding: 0px !important;
  margin-top: 0px;
  margin-bottom: 5px;
  font-size: 17px;
  text-align: left !important;
  }
  p.cartWidgetCantidad{
    font-size: 11px;
  }
  button{
    color: black;
  }
}

button.btnFinalizarCompra{
  width: 300px;
  border: solid 1px black;
  color: black;
  background-color: white;
}

button.btnFinalizarCompra:hover{
  width: 300px;
  border: solid 1px white;
  color: white;
  background-color: black;
}

li.cartWidgetFooter {
  justify-content: center;
  align-items: center
}




